import React, { useState } from "react";
import Frame from "../Frame/Frame";
import v6 from "../../../Assets/Images/png/SingleView/Vector (6).png";
import v5 from "../../../Assets/Images/png/SingleView/Vector (5).png";
import v4 from "../../../Assets/Images/png/SingleView/Vector (4).png";
import v04 from "../../../Assets/Images/png/SingleView/Vector04.png";

const Stacking = () => {
  const [isStaking, setIsStaking] = useState(true);

  const StakingData = [
    {
      id: 1,
      token_name: "$Yeti",
      token_value: "0.01₳",
      daily_token_reward: "-",
      daily_token_reward_value: "80$",
      ROI_bought_price: "200₳",
      ROI_at_floor: "15%",
    },
    {
      id: 2,
      token_name: "$Yeti",
      token_value: "0.01₳",
      daily_token_reward: "-",
      daily_token_reward_value: "80$",
      ROI_bought_price: "200₳",
      ROI_at_floor: "15%",
    },
  ];

  return (
    <Frame className="!p-3 bg-[#000000]">
      <div className="my-4">
        <div className="flex min-w-[740px] justify-between items-center my-4 px-8">
          <div className="flex justify-start items-center">
            <img src={v5} alt="" className="mr-3" />
            <p className="text-base md:text-sm text-white font-semibold">
              Staking
            </p>
          </div>
          {isStaking ? (
            <img
              src={v04}
              alt=""
              className="px-1 cursor-pointer"
              onClick={() => {
                setIsStaking(false);
              }}
            />
          ) : (
            <img
              src={v4}
              alt=""
              className="px-1 cursor-pointer"
              onClick={() => {
                setIsStaking(true);
              }}
            />
          )}
        </div>
        <div className="w-full border-b border-[#373737]" />

        {isStaking && (
          <div className="flex flex-col justify-start ">
            <div className="flex flex-row justify-between items-center my-2 px-8">
              <div className="w-20 text-lg md:text-sm whitespace-nowrap text-center text-white font-normal px-2">
                Token Name
              </div>
              <div className="w-20 text-lg md:text-sm whitespace-nowrap text-center text-white font-normal px-2">
                Token Value
              </div>
              <div className="w-20 text-lg md:text-sm  text-center text-white font-normal">
                Daily Token Reward
              </div>
              <div className="w-24 text-lg md:text-sm  text-center text-white font-normal ">
                Daily Token Reward Value
              </div>
              <div className="w-20 text-lg md:text-sm  text-center text-white font-normal">
                % ROI bought price
              </div>
              <div className="w-20 text-lg md:text-sm whitespace-nowrap text-center text-white font-normal px-2">
                % ROI at floor
              </div>
            </div>

            {StakingData?.length > 0 &&
              StakingData?.map((d, index) => (
                <div className="flex flex-row justify-between items-center border-t border-t-[#373737] py-2 px-8">
                  <div className="w-20 text-base md:text-sm text-center text-white font-normal px-2">
                    {d?.token_name}
                  </div>
                  <div className="w-20 text-base md:text-sm text-center text-white font-normal px-2">
                    {d?.token_value}
                  </div>
                  <div className="w-20 text-base md:text-sm text-center text-white font-normal px-2">
                    {d?.daily_token_reward}
                  </div>
                  <div className="w-20 text-base md:text-sm text-center text-white font-normal px-2">
                    {d?.daily_token_reward_value}
                  </div>
                  <div className="w-20 text-base md:text-sm text-center text-white font-normal px-2">
                    {d?.ROI_bought_price}
                  </div>
                  <div className="w-20 text-base md:text-sm text-center text-white font-normal px-2">
                    {d?.ROI_at_floor}
                  </div>
                </div>
              ))}
          </div>
        )}
      </div>
    </Frame>
  );
};

export default Stacking;
