import Container from "../Container/Container";

const Frame = ({ children, className }) => {
  return (
    <>
      <Container>
        <div className={`with-border-image ${className}`}>{children}</div>
        {/* <div className="wrap">
          <div className={`animated-border ${className}`}>
            <div className="animated-border1">{children}</div>
          </div>
        </div> */}
      </Container>
    </>
  );
};

export default Frame;
