import React, { useState } from "react";
import Frame from "../Frame/Frame";
import g57 from "../../../Assets/Images/png/SingleView/Group 57.png";
import g59 from "../../../Assets/Images/png/SingleView/Group 59.png";
import g60 from "../../../Assets/Images/png/SingleView/Group 60.png";
import g61 from "../../../Assets/Images/png/SingleView/Group 61.png";
import g62 from "../../../Assets/Images/png/SingleView/Group 62.png";
import g63 from "../../../Assets/Images/png/SingleView/Group 63.png";
import blueTick from "../../../Assets/Images/png/SingleView/correct 1.png";
import Comment from "../../../Assets/Images/png/SingleView/Comment.png";
import v1 from "../../../Assets/Images/png/SingleView/Vector (1).png";
import v2 from "../../../Assets/Images/png/SingleView/Vector (2).png";
import v3 from "../../../Assets/Images/png/SingleView/Vector (3).png";
import v4 from "../../../Assets/Images/png/SingleView/Vector (4).png";
import v04 from "../../../Assets/Images/png/SingleView/Vector04.png";
import v09 from "../../../Assets/Images/png/SingleView/Vector09.png";
import TabNavItem from "../../ReactTabs/TabNavItem";

const Description = () => {
  const [isProperties, setIsProperties] = useState(true);
  const [isAbout, setIsAbout] = useState(true);
  const [isDetails, setIsDetails] = useState(true);

  const PropertiesData = [
    {
      id: 1,
      title: "Background",
      detail: "Blue",
      percentage: "12% have this trait",
    },
    {
      id: 2,
      title: "Clothes",
      detail: "Caveman Pelt",
      percentage: "2% have this trait",
    },
    {
      id: 3,
      title: "Eyes",
      detail: "Closed",
      percentage: "10% have this trait",
    },
    {
      id: 4,
      title: "Head",
      detail: "Fez",
      percentage: "7% have this trait",
    },
    {
      id: 5,
      title: "Fur",
      detail: "Tan",
      percentage: "9% have this trait",
    },
    {
      id: 6,
      title: "Mouth",
      detail: "Bored Unshaven",
      percentage: "8% have this trait",
    },
  ];

  return (
    <Frame className=" bg-[#000000]">
      <div className="my-4">
        {/* section 1 ---------- */}
        <div className="flex justify-start items-center my-4 px-8">
          <img src={g57} alt="" className="mr-3" />
          <p className="text-base md:text-sm text-white font-semibold">
            Description
          </p>
        </div>
        <div className="w-full border-b-2 border-[#373737]" />
        <div className="flex flex-col justify-start text-xs md:text-sm text-white font-normal my-4 px-8">
          <div className="flex justify-start items-center">
            By <span className="font-bold px-2">Cosmic Astronauts</span>
            <img src={blueTick} alt="" className="w-3 h-3" />
          </div>
          <div className="w-full md:w-[80%] mt-2">
            After a very harsh ice age, polar bears were the only species that
            survived. Now, they need to explore the world, create inventions and
            a world of polar bears - cold, funky and definitely interesting!
            Fluffy Polar Bears are a collection of 9,999 randomly and fully
            On-Chain generated NFTs that exist on the Cardano Blockchain.
          </div>
        </div>
        <div className="w-full border-b-2 border-[#373737]" />

        {/* section 2 ---------- */}
        <div className="flex justify-between items-center my-4 px-8">
          <div className="flex justify-start items-center">
            <img src={v09} alt="" className="mr-3" />
            <p className="text-base md:text-sm text-white font-semibold">
              Properties
            </p>
          </div>
          <p className="text-lg md:text-base text-white font-normal">
            Rarity: #2631
          </p>
          {isProperties ? (
            <img
              src={v04}
              alt=""
              className="cursor-pointer"
              onClick={() => {
                setIsProperties(false);
              }}
            />
          ) : (
            <img
              src={v4}
              alt=""
              className="cursor-pointer"
              onClick={() => {
                setIsProperties(true);
              }}
            />
          )}
        </div>
        {isProperties && (
          <div className="grid grid-cols-1 md:grid-cols-2 my-4 px-8">
            {PropertiesData?.length > 0 &&
              PropertiesData?.map((d, index) => (
                <div
                  key={index}
                  className="flex justify-between items-start card-gradient-border bg-[#121212] m-3"
                >
                  <div className="flex flex-col justify-start pr-5">
                    <p className="text-sm md:text-xs text-[#2592D9] font-normal">
                      {d?.title}
                    </p>
                    <p className="text-base md:text-sm text-white font-normal">
                      {d?.detail}
                    </p>
                  </div>
                  <p className="text-base md:text-sm text-white font-normal">
                    {d?.percentage}
                  </p>
                </div>
              ))}
          </div>
        )}
        <div className="w-full border-b-2 border-[#373737]" />

        {/* section 3 ---------- */}
        <div className="flex justify-between items-center my-4 px-8">
          <div className="flex justify-start items-center">
            <img src={g59} alt="" className="mr-3" />
            <p className="text-base md:text-sm text-white font-semibold">
              About Fluffy Polar Bears
            </p>
          </div>
          {isAbout ? (
            <img
              src={v04}
              alt=""
              className="cursor-pointer"
              onClick={() => {
                setIsAbout(false);
              }}
            />
          ) : (
            <img
              src={v4}
              alt=""
              className="cursor-pointer"
              onClick={() => {
                setIsAbout(true);
              }}
            />
          )}
        </div>

        {isAbout && (
          <div className="flex justify-start items-center my-4 px-8">
            <div className="flex justify-start items-start">
              <img src={Comment} alt="" className="w-5 h-5 mr-3" />
              <div>
                <p className="text-xs md:text-sm text-white font-normal">
                  This collection has been migrated from{" "}
                  <a
                    href="https://opensea.io/collection/fpb-old-collection"
                    target={"_blank"}
                    className="underline"
                  >
                    https://opensea.io/collection/fpb-old-collection
                  </a>
                  . Originally minted on Oct 25th 2021. This is where fluffies
                  live now! Read more about our migration in our medium blog{" "}
                  <a
                    href="https://fluffypolarbears.medium.com/"
                    target={"_blank"}
                    className="underline"
                  >
                    https://fluffypolarbears.medium.com/
                  </a>
                </p>
                <p className="text-xs md:text-sm text-white font-normal mt-2">
                  “After a very harsh ice age, polar bears were the only species
                  that survived. Now, they need to explore the world, create
                  inventions and a world of polar bears - cold, funky and
                  definitely interesting!” Fluffy Polar Bears are a collection
                  of 9441 randomly and fully On-Chain generated NFTs that exist
                  on the Ethereum Blockchain. Polar Bears are hand-drawn by the
                  famous cartoonist{" "}
                  <a
                    href="https://twitter.com/selcukerdem"
                    target={"_blank"}
                    className="underline"
                  >
                    Selcuk Erdem
                  </a>
                </p>
                <p className="text-xs md:text-sm text-white font-normal mt-2">
                  Category PFPs
                </p>
                <div className="max-w-fit flex justify-start items-center bg-[#121212] border border-[#4B4B4B] rounded-lg cursor-pointer mt-5">
                  <img
                    src={v1}
                    alt="icon"
                    className="p-3 border-r border-r-[#4B4B4B]"
                  />
                  <img
                    src={v2}
                    alt="icon"
                    className="p-3 border-r border-r-[#4B4B4B]"
                  />
                  <img
                    src={v3}
                    alt="icon"
                    className="p-3 border-r border-r-[#4B4B4B]"
                  />
                  <img
                    src={g61}
                    alt="icon"
                    className="p-3 border-r border-r-[#4B4B4B]"
                  />
                  <img src={g62} alt="icon" className="p-3 px-5" />
                </div>
              </div>
            </div>
          </div>
        )}
        <div className="w-full border-b-2 border-[#373737]" />

        {/* section 4 ---------- */}
        <div className="flex justify-between items-center my-4 px-8">
          <div className="flex justify-start items-center">
            <img src={g60} alt="" className="mr-3" />
            <p className="text-base md:text-sm text-white font-semibold">
              Details
            </p>
          </div>
          {isDetails ? (
            <img
              src={v04}
              alt=""
              className="cursor-pointer"
              onClick={() => {
                setIsDetails(false);
              }}
            />
          ) : (
            <img
              src={v4}
              alt=""
              className="cursor-pointer"
              onClick={() => {
                setIsDetails(true);
              }}
            />
          )}
        </div>
        {isDetails && (
          <div className="my-4 px-8">
            <div className="flex justify-between items-start">
              <div className="flex flex-col justify-start gap-1 text-xs md:text-sm text-white font-normal pr-5 ">
                <p>Contract Address</p>
                <p>Token ID</p>
                <p>Token Standard</p>
                <p>Chain</p>
                <div className="flex justify-start items-center">
                  <p className="mr-2">Creator Fee</p>
                  <img
                    src={g63}
                    alt="info"
                    className="w-2 h-2 cursor-pointer"
                  />
                </div>
              </div>
              <div className="flex flex-col justify-end gap-1 text-xs md:text-sm text-white font-normal">
                <a
                  href="https://etherscan.io/address/0x98ab7eaacb2f5157a40a9d1de6c2abd1a73ef9fa"
                  target={"_blank"}
                  className="underline"
                >
                  0x98ab...f9fa
                </a>
                <a
                  href="https://polarbearsnft.mypinata.cloud/ipfs/QmTn7HTq9Tt9HwJADRAXNcqwJ7qdn7DNc2oQJbuakU2n12/6322"
                  target={"_blank"}
                  className="underline"
                >
                  6322
                </a>
                <p>ERC-721</p>
                <p>Ethereum</p>
                <p>5%</p>
              </div>
            </div>
          </div>
        )}
      </div>
    </Frame>
  );
};

export default Description;
