import React from "react";

const TabNavItem = ({ id, title, activeTab, setActiveTab, className }) => {
  const handleClick = () => {
    setActiveTab(id);
  };
  return (
    <button
      onClick={handleClick}
      className={`transition-all delay-150 ease-in-out ${
        activeTab === id
          ? "tab-active transition-all delay-150 ease-in-out"
          : "gradient-border-rounded transition-all delay-150 ease-in-out"
      } ${className}`}
    >
      {title}
    </button>
  );
};

export default TabNavItem;
