import React from "react";
import profile from "../../../Assets/Images/png/SingleView/profile.png";
import g42 from "../../../Assets/Images/png/SingleView/Group 42.png";
import g48 from "../../../Assets/Images/png/SingleView/Group 48.png";
import g49 from "../../../Assets/Images/png/SingleView/Group 49.png";
import vector from "../../../Assets/Images/png/SingleView/Vector.png";
import Frame from "../Frame/Frame";

import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";

const ImagePreview = () => {
  return (
    <Frame className=" bg-[#0D0D0D]">
      <div className="flex flex-col justify-center items-center m-1">
        <div className="relative">
          <LazyLoadImage
            width={600}
            height={600}
            src={profile}
            alt="profile"
            effect="blur"
          />
        </div>
        <div className="flex flex-row justify-between items-center my-2">
          <img className="mx-2" src={g42} alt="" />
          <div className="flex justify-center items-center mx-5">
            <div className="flex justify-center items-center m-2">
              <img src={g49} alt="" className="mx-2" />
              <p className="text-sm md:text-xs text-white font-normal">
                130 views
              </p>
            </div>
            <div className="flex justify-center items-center m-2">
              <img src={vector} alt="" className="mx-2" />
              <p className="text-sm md:text-xs text-white font-normal">
                3 favorites
              </p>
            </div>
            <div className="flex justify-center items-center m-2">
              <img src={g48} alt="" className="mx-2" />
              <p className="text-sm md:text-xs text-white font-normal">PFPs</p>
            </div>
          </div>
          <img className="mx-2 cursor-pointer" src={vector} alt="" />
        </div>
      </div>
    </Frame>
  );
};

export default ImagePreview;
