import React, { useState } from "react";
import Frame from "../Frame/Frame";
import v6 from "../../../Assets/Images/png/SingleView/Vector (6).png";
import v5 from "../../../Assets/Images/png/SingleView/Vector (5).png";
import v4 from "../../../Assets/Images/png/SingleView/Vector (4).png";
import v04 from "../../../Assets/Images/png/SingleView/Vector04.png";
import Description from "./Description";
import ListingWithButton from "./ListingsWithButton";

import ImagePreview from "./ImagePreview";
import Correct from "./correct.svg";
import { ButtonFill } from "../Button/Button";
import Stacking from "./Stacking";

const Listings = () => {
  const [isListings, setIsListings] = useState(true);

  const ListingsData = [
    {
      id: 1,
      price: "0.1514",
      usd_price: "$181.19",
      expiration: "6 days",
      from: "HardTimes00",
      link: "https://opensea.io/HardTimes00",
    },
    {
      id: 2,
      price: "0.1514",
      usd_price: "$181.19",
      expiration: "6 days",
      from: "HardTimes00",
    },
    {
      id: 3,
      price: "0.1514",
      usd_price: "$181.19",
      expiration: "6 days",
      from: "HardTimes00",
      link: "https://opensea.io/HardTimes00",
    },
    {
      id: 4,
      price: "0.1514",
      usd_price: "$181.19",
      expiration: "6 days",
      from: "HardTimes00",
      link: "https://opensea.io/HardTimes00",
    },
    {
      id: 5,
      price: "0.1514",
      usd_price: "$181.19",
      expiration: "6 days",
      from: "HardTimes00",
      link: "https://opensea.io/HardTimes00",
    },
    {
      id: 6,
      price: "0.1514",
      usd_price: "$181.19",
      expiration: "6 days",
      from: "HardTimes00",
      link: "https://opensea.io/HardTimes00",
    },
  ];

  return (
    <>
      <div className="pt-[75px] min-h-screen container mx-auto">
        <div className="grid lg:grid-cols-2 ">
          <div className="md:w-10/12">
            <ImagePreview />
            <div className="my-4">
              <Description />
            </div>
          </div>
          <div>
            <div className="flex items-center gap-4 mb-4">
              <h4 className="font-helvetica text-2xl font-medium">
                Cosmic Astronauts
              </h4>
              <img src={Correct} alt={Correct} />
            </div>
            <h4 className="font-helvetica text-3xl font-bold">
              BeanBag Fren #7410
            </h4>
            <div className="flex justify-between items-center">
              <h4 className="text-xl font-normal font-helvetica">
                Owned by &nbsp;
                <span className="underline-offset-8 underline">
                  TheArtfulRodger
                </span>
              </h4>
              <ButtonFill>Edit</ButtonFill>
            </div>
            <div className="my-10">
              <Stacking />
            </div>
            <div className="my-10">
              <ListingWithButton />
            </div>
            <div className="my-10">
              <Frame className="!p-3 bg-[#000000] mt-10">
                <div className="my-4">
                  <div className="flex min-w-[740px] justify-between items-center my-4 px-8">
                    <div className="flex justify-start items-center">
                      <img src={v5} alt="" className="mr-3" />
                      <p className="text-base md:text-sm text-white font-semibold">
                        Listings
                      </p>
                    </div>
                    {isListings ? (
                      <img
                        src={v04}
                        alt=""
                        className="px-1 cursor-pointer"
                        onClick={() => {
                          setIsListings(false);
                        }}
                      />
                    ) : (
                      <img
                        src={v4}
                        alt=""
                        className="px-1 cursor-pointer"
                        onClick={() => {
                          setIsListings(true);
                        }}
                      />
                    )}
                  </div>
                  <div className="w-full border-b border-[#373737]" />

                  {isListings && (
                    <div className="flex flex-col justify-start">
                      <div className="flex flex-row justify-between my-2 px-8">
                        <div className="w-40 text-lg md:text-base text-white font-normal">
                          Price
                        </div>
                        <div className="w-40 text-lg md:text-base text-white font-normal">
                          USD Price
                        </div>
                        <div className="w-40 text-lg md:text-base text-white font-normal">
                          Expiration
                        </div>
                        <div className="w-48 text-lg md:text-base text-white font-normal">
                          From
                        </div>
                      </div>

                      {ListingsData?.length > 0 &&
                        ListingsData?.map((d, index) => (
                          <div className="flex flex-row justify-between border-t border-t-[#373737] py-2 px-8">
                            <div className="w-40 text-base md:text-sm text-white font-normal">
                              {d?.price}
                            </div>
                            <div className="w-40 text-base md:text-sm text-white font-normal">
                              {d?.usd_price}
                            </div>
                            <div className="w-40 text-base md:text-sm text-white font-normal">
                              {d?.expiration}
                            </div>
                            <div className="w-48 flex justify-between items-center text-base md:text-sm text-white font-normal ">
                              <a
                                href={d?.link}
                                target="_blank"
                                className="underline"
                                rel="noreferrer noopener"
                              >
                                {d?.from}
                              </a>
                            </div>
                          </div>
                        ))}
                    </div>
                  )}
                </div>
              </Frame>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Listings;
